.shop-main{
  
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(1fr, 200px));
    grid-gap: 30px;
    justify-content: center;
    font-family: $fontFamily;
    margin-left: 1%;
    margin-right: 1%;
    color: $bodyColor;
    h2{
      background-color: $backgroundColor2;
      text-align: center;
    }
    
    #figurines, #shirts, #towels, #books, #mugs{
      flex-wrap: wrap;
      display: flex;
      align-items: end;
      margin-top: 10px;
      margin-bottom: 10px;
      border-bottom: 2px solid $headerBGcolor;
      position: relative;
        div{
          transition: transform .5s ease;
          &:hover{
            transform: scale(1.1);
          }
        }
  }

        span{
          background-color: black;
          position: absolute;
          font-size: .7em;
          margin: 2px;
          z-index: 10;
        }
  
          img{
            flex-wrap: wrap;
            margin: 5px;
            height: 250px;
            width: auto;
            background-color: rgba(86, 73, 73, 0.103);
            border-top: 4px solid $headerBGcolor;
            border-bottom: 4px solid $headerBGcolor;
            transition: transform .5s ease;
            &:hover{
              background-color: rgba(228, 213, 213, 0.466);
              
            }
          }
        }
  
        footer{
          background-color: $headerBGcolor;
          height: auto;
          letter-spacing: 12px;
          text-align: center;
        }